import React, { useEffect, useState, useRef } from "react";
import suneditor from "suneditor";
import en from "suneditor/src/lang/en";
import { align } from "suneditor/src/plugins";

const DEFAULT_EDITOR_OPTIONS = {
  plugins: [align],

  value: "",
  toolbarContainer: "",
  buttonList: [],
  font: [],
  tableCellControllerPosition: "bottom",
  showPathLabel: false,
  resizingBar: false,
  defaultStyle: "",
  historyStackDelayTime: 100,
  attributesWhitelist: {
    all: "style|data-childtable|src",
  },
  addTagsWhitelist: "img|mark",
  imageUploadUrl: "",
  frameAttrbutes: {
    spellcheck: false,
  },
  placeholder: "",
};

export function MyComponent() {
  const editorRef = useRef(null);
  const editorInstanceRef = useRef(null);

  const [isEditorReady, setIsEditorReady] = useState(false);

  useEffect(function InitEditor() {
    editorInstanceRef.current = suneditor.create(editorRef.current, {
      ...DEFAULT_EDITOR_OPTIONS,
      ...editorOptions,
      plugins: editorPlugins,
    });

    if (getEditorInstance) getEditorInstance(editorInstanceRef.current);
    if (onSave) editorInstanceRef.current.onSave = (content) => onSave(content);
    setIsEditorReady(true);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <>
      <div ref={editorRef} />
      {isEditorReady && (
        <CustomPluginPortals {...rest} ref={editorInstanceRef} />
      )}
    </>
  );
}
